@charset "UTF-8";

/* 포인트 내역 테이블 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-history {
    margin-top: 20px;
}

.wrap .hmk_main-container .hmk_main .hmk_point-history h2 {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}

/* 포인트 요약 박스 스타일 */
.wrap .hmk_main-container 
.hmk_main 
.hmk_point-history 
.hmk_point-summary {
    display: flex; /* 플렉스 박스를 사용하여 가로 배열 */
    margin-bottom: 20px; /* 아래 여백 추가 */
    gap:10px;
}
.wrap .hmk_main-container 
.hmk_main 
.hmk_point-history 
.hmk_point-summary
.hmk_point-box {
    width: 30%; /* 각 박스의 너비를 설정 */
    padding: 20px; /* 패딩 추가 */
    gap: 15px;
    background-color: #f4f4f4; /* 박스 배경색 */
    border: 1px solid #ddd; /* 테두리 색상 */
    border-radius: 8px; /* 모서리 둥글게 */
    display: flex; /* 플렉스 박스를 사용하여 세로 정렬 */
    flex-direction: column; /* 세로 방향으로 배열 */
    align-items: center; /* 수평 중앙 정렬 */
    text-align: center; /* 텍스트 중앙 정렬 */
}
.wrap .hmk_main-container 
.hmk_main
.hmk_point-history 
.hmk_point-summary
.hmk_point-box 
.hmk_point-value {
    font-size: 24px; /* 숫자 크기 조정 */
    font-weight: bold; /* 숫자 강조 */
}

/*************** 테이블 날짜 필터 컨테이너 ***************/
.wrap .hmk_main-container 
.hmk_main
.hmk_point-history
.hmk_point_filterbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    border-radius: 5px;
    box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.2);
}

/* 토글 버튼 컨테이너 스타일 */
.wrap .hmk_main-container .hmk_toggle-container {
    display: flex; /* 수평 정렬 */
    margin-left: 30px;
    margin-bottom: 20px; /* 아래 여백 추가 */
    border-radius: 20px;
    box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.05);
}

/* 토글 버튼 기본 스타일 */
.wrap .hmk_main-container .hmk_toggle-button {
    padding: 5px 30px; /* 버튼 패딩 */
    border: none; /* 테두리 */
    background-color: white; /* 기본 배경색 */
    cursor: pointer; /* 마우스 커서 변경 */
    transition: background-color 0.3s; /* 배경색 전환 효과 */
    width: max-content;
}
/* 좌측 버튼 스타일 (둥글게) */
.wrap .hmk_main-container .hmk_toggle-button:first-child {
    border-top-left-radius: 20px; /* 왼쪽 위 모서리 둥글게 */
    border-bottom-left-radius: 20px; /* 왼쪽 아래 모서리 둥글게 */
}
/* 중앙 버튼 스타일 (네모) */
.wrap .hmk_main-container .hmk_toggle-button:nth-child(2) {
    border-radius: 0; /* 모서리 둥글게 하지 않음 */
}
/* 우측 버튼 스타일 (둥글게) */
.wrap .hmk_main-container .hmk_toggle-button:last-child {
    border-top-right-radius: 20px; /* 오른쪽 위 모서리 둥글게 */
    border-bottom-right-radius: 20px; /* 오른쪽 아래 모서리 둥글게 */
}
/* 활성화된 버튼 스타일 */
.wrap .hmk_main-container .hmk_toggle-button.active {
    background-color: #f8d7da; /* 활성화된 버튼의 배경색 */
    border: 1px solid #f8d7da;
}
/* 호버 효과 */
.wrap .hmk_main-container .hmk_toggle-button:hover {
    background-color: #e9ecef; /* 호버 시 배경색 변경 */
}
.wrap .hmk_main-container .hmk_toggle-button.active:hover {
    background-color: #f2cccf; /* 활성화된 버튼의 배경색 */
    border: 1px solid #f8d7da;
}

/* 날짜 필터 박스 스타일 */
.wrap .hmk_main-container 
.hmk_main
.hmk_point-history
.hmk_point_filterbar
.hmk_point-filter {
    display: flex;
    justify-content: flex-end;
    padding: 15px 15px 0 15px;
    height: 90px;
}
/* 날짜 필터 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-filter p {
    font-size: small;
    padding: 12px 0 12px 0;
}
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-inputs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 15px 0 15px 0;
}
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-inputs .hmk_date {
    border-radius: 8px;  /* 모서리 둥글게 */
}
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-inputs .hmk_date label {
    font-size: 10px;
}
/* 날짜 선택 버튼 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-options {
    margin-right: 10px;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    align-content: center;
    flex-wrap: wrap;
    width: max-content;
}
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-options .hmk_date-button {
    height: fit-content;
    margin-right: 5px; /* 버튼 간격 */
    padding: 8px 10px; /* 버튼 패딩 */
    border: none; /* 기본 테두리 제거 */
    border-radius: 5px; /* 모서리 둥글게 */
    background-color: #e9ecef; /* 버튼 배경색 */
    cursor: pointer; /* 마우스 커서 변경 */
    transition: background-color 0.3s; /* 배경색 전환 효과 */
}
/* 버튼 호버 효과 */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-options .hmk_date-button:hover {
    background-color: #f8d7da; /* 호버 시 배경색 변경 */
}
/* 날짜 입력 필드 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-wrapper {
    position: relative; /* 절대 위치 조정을 위해 설정 */
    display: flex; /* 플렉스 박스를 사용하여 수평 정렬 */
    align-items: center; /* 수직 중앙 정렬 */
    margin-bottom: 15px; /* 아래 여백 추가 */
}
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-input {
    width: 150px; /* 입력 필드 너비 */
    padding: 10px; /* 패딩 추가 */
    margin-right: 10px; /* 오른쪽 여백 추가 */
    border: 1px solid #ccc; /* 테두리 추가 */
    border-radius: 5px; /* 모서리 둥글게 */
    outline: none;
}
/* 검색 버튼 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_search-button {
    padding: 10px 20px; /* 버튼 패딩 */
    background-color: #007bff; /* 버튼 배경색 */
    color: white; /* 글자색 */
    border: none; /* 기본 테두리 제거 */
    border-radius: 5px; /* 모서리 둥글게 */
    cursor: pointer; /* 마우스 커서 변경 */
    transition: background-color 0.3s; /* 배경색 전환 효과 */
    width: max-content;
}
/* 검색 버튼 호버 효과 */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_search-button:hover {
    background-color: #0056b3; /* 호버 시 배경색 변경 */
}
/*************** /테이블 날짜 필터 컨테이너 ***************/


/*************** 테이블 컨테이너 ***************/
.wrap .hmk_main-container .hmk_main .hmk_point-table {
    width: 100%; /* 전체 너비 */
    table-layout: fixed; /* 고정된 레이아웃 */
    border-collapse: collapse; /* 테두리 겹침 제거 */
    margin-top: 10px;
}
/* 제목 필드 */
.wrap .hmk_main-container .hmk_main .hmk_point-table thead {
    border-bottom: 1px solid #ededed;
    
}
/* 테이블 셀 설정*/
.wrap .hmk_main-container .hmk_main .hmk_point-table th,
.wrap .hmk_main-container .hmk_main .hmk_point-table td {
    padding: 10px; /* 셀 내 여백 */
    text-align: center; 
    overflow: hidden; /* 넘치는 텍스트 숨김 */
    text-overflow: ellipsis; /* 넘치는 텍스트를 '...'로 표시 */
    white-space: nowrap;  /* 텍스트 줄바꿈 방지 */
}
.wrap .hmk_main-container .hmk_main .hmk_point-table tr:nth-child(even) {
    background-color: #f9f9f9; /* 짝수 줄 배경색 */
}
/* 포인트 내역 섹션 내 포인트 테이블의 적립 포인트 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-history .hmk_point-table tbody tr td .earned {
    color: #007BFF;
    font-weight: bold;
}
/* 포인트 내역 섹션 내 포인트 테이블의 사용 포인트 스타일 */
.wrap .hmk_main-container .hmk_main .hmk_point-history .hmk_point-table tbody tr td .spent {
    color: #000;
    font-weight: bold;
}